import { useContext, useState } from "react";
import style from "./Form.module.css";
import { ShopContext } from "../../context/context";
import { useForm } from "react-hook-form";
import axios from "axios";

import Modal from "../Modal/Modal";

export const Form = () => {

  // const [ sent, setSent ] = useState(false);
  const [modalActive, setModalActive] = useState(false);
  const { setDataForm, dataForm } = useContext(ShopContext);
  let randomNumber = Math.floor(Math.random() * (999999 - 100000 + 1)) + 100000;

  const {
    register,
    handleSubmit,
    formState: { errors, isValid, isDirty },
    reset,
  } = useForm({
    mode: "onChange",
    defaultValues: { name: "", phone: "", email: "" }
  });

  const handleSend = async (data) => {
    // setSent(true);
    // data.randomNumber = randomNumber;
		try {
			await axios.post("http://localhost:4000/send_mail", {
				data
			})
		} catch (error) {
			console.error(error)
		}
	}

  console.log(dataForm)
  function handleFormSubmit(data) {
    data.randomNumber = randomNumber;
    setDataForm(data);
    handleSend(data);
    setModalActive(true);
    reset();
  }

  return (
    <>
      <div className={style.formSection}>
        <div className="container">
          <div className={style.wrapper}>
            <form
              className={style.form}
              onSubmit={handleSubmit(handleFormSubmit)}
            >
              <h3 className={style.title}>, </h3>
              <input
                name="name"
                className={!errors?.name ? style.inputForm : style.inputFormError}
                type="text"
                {...register("name", {
                  required: "   ",
                  minLength: {
                    value: 5,
                    message: "  5 "
                  }
                })}
                placeholder=" "
                autoComplete="off"
              />
              <div>
                {errors?.name && <p className={style.errorMessage}>{errors?.name.message}</p>}
              </div>
              <input
                name="phone"
                className={!errors?.phone ? style.inputForm : style.inputFormError}
                type="phone"
                {...register("phone", {
                  required: "   ",
                  minLength: {
                    value: 11,
                    message: "  11 "
                  }
                })}
                placeholder=""
                autoComplete="off"
              />
              <div>
                {errors?.phone && <p className={style.errorMessage}>{errors?.phone.message}</p>}
              </div>
              <input
                name="email"
                className={!errors?.email ? style.inputForm : style.inputFormError}
                type="email"
                {...register("email", {
                  required: "   ",
                  pattern: {
                    value: /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/,
                    message: "email   "
                  }
                },
                )}
                placeholder="Email"
                autoComplete="off"
              />
              <div>
                {errors?.email && <p className={style.errorMessage}>{errors?.email.message}</p>}
              </div>
              <div className={style.buttonForm}>
                <button type="submit" disabled={!isDirty || !isValid} className={style.buttonBlue}> </button>
              </div>
            </form>
          </div>
        </div>
      </div>
      <Modal
        active={modalActive}
        setActive={setModalActive}
        dataForm={dataForm}
      >
        <h4 className={style.messageTitle}> <span>{dataForm.name},</span>   <span>{randomNumber}</span> .</h4>
        <h6 className={style.messageText}>         <span>{dataForm.phone}</span>   .</h6>
      </Modal>
    </>
  )
}
